/* ============================ */
/* Styles used throughout page */
/* ============================ */
.home-button {
    width: -webkit-fit-content;
    width: fit-content;
    padding: 1.5vh 1vw;
    text-decoration: none;
    margin: 6vh 0.5vw;
    border-radius: 50px;
    transition: 0.2s;
}

.home-button {
    color: white;
    background: #6FCF97;
}

.home-button:hover {
    background-color: #4DAB74;
    text-decoration: none;
    color: white;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    overflow: hidden;
    margin: 0;
}

.section {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 75vw;
    margin: 8vh;
}

.section-img-container {
    position: relative; 
}

.text-left, .text-right {
    margin-bottom: 12vh;
}

#about {
    z-index: 3;
}

#project {
    z-index: 2;
}

#community {
    z-index: 1;
}

.text {
    position: relative;
    width: 25vw;
    display: flex;
    flex-direction: column;
}

.text p {
    margin-bottom: 4vh;
}

.text.projects {
    margin-left: 8vw;
}

/* ============================ */
/* Images */
/* ============================ */
.section-img {
    width: 30vw;
    min-width: 350px;
    border-radius: 25px;
}

.carousel {
    width: 25vw;
    min-width: 300px;
    border-radius: 25px;
}

.carousel-cell img {
    width: 25vw;
    min-width: 300px;
    border-radius: 25px;
}
/* ============================ */

/* ============================ */
/* Background Puzzle Pieces & People Illustrations */
/* ============================ */

.puzzle {
    position: absolute;
    opacity: 0.6;
    z-index: -5;
}

.puzzle img {
    position: absolute;
    opacity: 0.6;
}

.big img {
    height: 75vh;
    top: -10vh;
    left: -7.5vw;
    -webkit-transform: rotate(152deg);
            transform: rotate(152deg);
}

.big2 img {
    height: 70vh;
    -webkit-transform: rotate(75deg);
            transform: rotate(75deg);
    top: -15vh;
    left: -5vw;
}

.big3 img {
    height: 66vh;
    -webkit-transform: rotate(200deg);
            transform: rotate(200deg);
    top: -15vh;
    left: -7.5vw;
}

.person {
    position: absolute;
    z-index: 1;
}

.sitting {
    position: absolute;
    bottom: -10%;
    left: -15%;
}

.sitting img {
    width: 100px;
    height: auto;
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
}

.standing {
    position: absolute;
    bottom: -10%;
    left: 60%;
}

.standing2 {
    position: absolute;
    bottom: -10%;
    left: 15%;
}

.standing img, .standing2 img {
    width: auto;
    height: 125px;
}

/* ============================ */

/* Portrait */
@media screen and (max-width: 900px) {
    .home-button {
        font-size: 16px;
        padding: 1.5vh 2vw;
        margin: .75vh;
        border-radius: 25px;
    }

    .home-button {
        margin-top: 4vh;
    }

    .content {
        margin-bottom: 6vh;
    }

    .section {
        margin: 7.5vh 0;
    }

    .text-left {
        flex-direction: column;
    }

    .text-right {
        flex-direction: column-reverse;
        justify-content: baseline;
    }

    .text {
        width: 86vw;
        text-align: left;
    }

    .section-img, .carousel, .carousel-cell img {
        width: 50vw;
        height: auto;
        margin-top: 4vh;
    }

    .carousel, .carousel-cell img {
        width: 40vw;
    }

    .big2 {
        left: 50vw;
    }

    .text.projects {
        margin-left: 0;
    }
}

@media screen and (max-width: 600px) {
    .section-img, .carousel, .carousel-cell img {
        width: 65vw;
    }

    .sitting img {
        display: none;
    }

    .standing img, .standing2 img {
        height: 75px;
    }

    .puzzle img {
        height: 50vh;
    }
}
