/* 
Design guide link: https://www.notion.so/Website-Design-Guide-8b8c29e207ab476d95b9ea567ba1a0b7
*/

/* ============================ */
/* Styles used throughout page */
/* ============================ */
:root {
  --darkgreen: #3f8f2f;
  --forestgreen: #4DAB74;
  --seagreen: #57BE97;
  --mintgreen: #AFF1B9;
  --yellowish: #F2F3B7;
  --gold: #DB9E56;
  --lmintgreen: #DCF4D4;
  --almostwhite:#D5EAE2;
  --white: #ffff;
  --black: #000000;
  --darkgrey: #202020;
  --grey: #5E5E5E;
}

* {
  box-sizing: border-box;
  font-family: 'Karla';
  font-weight:  bold;
  color: #202020;
  color: var(--darkgrey);
}

body {
  margin: 0;
}

/* shared classes */
.hidden {
  display: none;
}

/*font sizes*/
h1, h2, .h2, h3, .h3, b, .b {
  font-weight: bold;
}

/* Title of pages: see WallImage (about us, projects, etc.) */
h1{
  font-size: 60px;
}

/* Headers/Sections: see Header */
h2, .h2 {
  font-size: 36px !important;
  font-weight: bold;
}

/* Emphasized descriptions/smaller titles: see PageDescription */
h3, .h3 {
  font-size: 20px;
  font-weight: bold;
}

/* Longer descriptions */
p {
  font-size: 16px;
  font-weight: 400;
}

.subtext {
  font-size: 14px;
}

a, a:hover {
  color: #26976c;
  /* a darker seagreen */
}

strong {
  color: rgba(0, 0, 0, 0.84);
}

@media (max-width: 767px) {
  h1 {
    font-size: 52px;
  }

  h2, .h2 {
    font-size: 24px !important;
  }

  h3, .h3 {
    font-size: 16px;
  }

  p, b, .b {
    font-size: 14px;
  }

  .subtext {
    font-size: 12px;
  }
}

.side-drawer {
    height: 100%;
    background: white;
    /* box-shadow: 1px 0px 7px; */
    position: fixed;
    top: 0;
    right: 0;
    width: 60%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-drawer ul {
    list-style: none;
    margin-top: 10%;
}

.side-drawer a {
    text-decoration: none;
    color: var(--darkgrey);
    font-size: 24px;
}

.side-drawer li {
    margin-bottom: 30px;
}

@media screen and (max-width: 767px) {
    .side-drawer a {
        font-size: 20px;
    }

    .side-drawer li {
        margin-bottom: 24px;
    }
}

